const string =`.skin *{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
.skin *::before,.skin *::after{box-sizing: border-box;}


.skin{
    min-height: 60vh;
    position: relative;
    background: rgb(255,230,0);
    
}

.san{
    border: 10px solid ;
    border-color: black transparent transparent;
    width: 0px;
    height: 0px;
    position: relative;
    left: 50%;
    top: 195px;
    margin-left: -10px;
}
.triangle{
    background: black;
    width: 20px;
    height: 7px;
    position: absolute;
    left: -10px;
    top: -17px;
    border-radius: 14px 14px 0 0;
}
.eye{
    border: 3px solid rgb(0,0,0);
    width: 64px;
    height: 64px;
    background: rgb(46,46,46);
    position: absolute;
    left: 50%;
    margin-left: -32px;
    top: 150px;
    border-radius:50%;
}
.eye.left{
    transform: translate(120px);
    
    
}
.eye.right{
    transform: translate(-120px);
}
.eye.left::before{
    content: '';
    border: 3px solid black;
    width: 25px;
    height: 25px;
    position: absolute;
    background: white;
    border-radius: 50%;
    left: 8px;
    top:1px;
}
.eye.right::before{
    content: '';
    border: 3px solid black;
    width: 25px;
    height: 25px;
    position: absolute;
    background: white;
    border-radius: 50%;
    left: 8px;
    top:1px;
}

.mouth .up{
    
    width: 200px;
    position: relative;
    left:50%;
    margin-left:-100px;
    top: 195px;
    z-index: 1;
    
}
.mouth .up .lip{
    position: absolute;
    border: 5px solid black;
    width: 87px;
    height: 25px;
    border-top: transparent;
    background: rgb(255,230,0);
}
.mouth .up .lip.left{
    transform: translate(12px,5px)rotate(-15deg);
    border-right: none;
    border-bottom-left-radius:50px;
}
.mouth .up .lip.right{
    right: 0;
    border-radius:0 0 45px 0px;
    transform: translate(-12px,5px)rotate(15deg);
    border-left: transparent;
}
.mouth .down{
    
    position: relative;
    width: 180px;
    height: 180px;
    left: 50%;
    margin-left: -90px;
    top: 210px;
    overflow: hidden;
}
.mouth .tongue{
    
    position: relative;
    width: 140px;
    left: 20px;
    height: 380px;
    top: -240px;
    border-radius: 50%;
    background: #9B000A;
    overflow: hidden;
}
.tongue2{
   
    background: rgb(255,72,95);
    position: absolute;
    top: 260px;
    width: 140px;
    height: 210px;
    border-radius:50% ;
}
.face{
    border: 3px solid black;
    position: absolute;
    width: 88px;
    height: 88px;
    left: 50%;
    margin-left: -44px;
    border-radius: 50%;
    background: rgb(255,0,0);
    top: 260px;
}
.face.left{
    transform: translateX(180px);
}
.face.right{
    transform: translateX(-180px);
}
@keyframes wave{
    0%{
        transform: rotate(0deg);
    }
    33%{
        transform: rotate(5deg);
    }
    66%{
        transform: rotate(-5deg);
    }
    100%{
        transform: rotate(0deg);
    }

}
.san:hover{
    transform-origin:center bottom ;
    animation: wave 300ms infinite linear;
}
`
export default string;